import core from './core'
import Vue from 'vue'
import {ClientTable} from 'vue-tables-2'
import Notifications from 'vue-notification'
import VeeValidate, { Validator } from 'vee-validate'
import VeeValidateRu from 'vee-validate/dist/locale/ru'
import VueSocketio from 'vue-socket.io'

//
Vue.use(ClientTable, {
  texts:{
    count:"  {from}  {to}  {count} |{count} | ",
    filter:": ",
    filterPlaceholder:" ",
    limit:":",
    page:":",
    noResults:"  ",
    filterBy:"  {column}",
    loading:'...',
    defaultOption:' {column}',
    columns:''
  },
}, false, 'bootstrap3', 'default');

//
const notify = {
  install (Vue, options) {
    Vue.mixin({
      methods: {
        notify (text, type = 'success', options = {}) {
          if (type === 'danger') type = 'error'
          this.$notify({
            ...options,
            text: text,
            type: type,
          })
        }
      }
    })
  },
}
Vue.use(Notifications)
Vue.use(notify)

//
Validator.localize('ru', VeeValidateRu);
Vue.use(VeeValidate, {
  locale: 'ru',
  dictionary: {
    ru: {attributes: {
      login: '',
      email: 'Email',
      fullname: '..',
      department: '',
      position: '',
      phone: '',
      whatsapp: 'Whatsapp',
      name: '',
      description: '',
      text: '',
      urgency: '',
      to: '',
      memoTo: '',
      deadline: ' ',
    }},
  }
})

Vue.use(VueSocketio, core.$config('app.soketUrl'))


